import _ from 'lodash';
import graphme from '@unitz/graphme';

import hasuraClient from '@vl/mod-clients/hasuraApp';

import GetCategory from 'unitz-gql/category/GetCategory.gql';

graphme.GqlBuilder.loadDocument({
  GetCategory
});

class CategoryModel extends graphme.BaseModel {
  static DEFINITION = graphme.Definition.create({
    name: 'CategoryModel',
    schema: {
      id: String,
      display_name: String
    },

    key: 'id',

    baseQuery: 'GetCategory',
    GQL_ACTIONS: {
      GET: 'category_by_pk',
      FIND: 'category',
      INSERT: 'insert_category_one',
      UPDATE: 'update_category_by_pk',
      DELETE: 'delete_category_by_pk'
    },
    getClient: hasuraClient.getClient
  });

  static async addNewCategory({ advisor_id, category_id }) {
    const query = hasuraClient.gql`
      mutation addNewCategory($advisor_id: uuid!, $category_id: String!) {
        profile: insert_advisor_profile_category(objects: {category_id: $category_id, advisor_profile_id: $advisor_id}, on_conflict: {constraint: advisor_profile_category_advisor_profile_id_category_id_key, update_columns: created_at}) {
          returning {
            id
          }
        }
      }
    `;
    try {
      const rtn = await hasuraClient.getClient().request(query, {
        category_id,
        advisor_id
      });

      const data = _.get(rtn, 'profile.returning');
      if (data) {
        return data;
      }
    } catch (err) {
      console.log(err);
    }

    return null;
  }

  static async removeNewCategory({ id }) {
    const query = hasuraClient.gql`
      mutation removeNewCategory($id: String!) {
        profile: delete_advisor_profile_category(where: {category_id: {_eq: $id}}) {
          returning {
            id
          }
        }
      }
    `;
    try {
      const rtn = await hasuraClient.getClient().request(query, {
        id
      });

      const data = _.get(rtn, 'profile.returning');
      if (data) {
        return data;
      }
    } catch (err) {
      console.log(err);
    }

    return null;
  }
}

graphme.model({ CategoryModel });

export default CategoryModel;
